<template>
  <main>
        <div id="list"  @click="goDetail(index)" v-for='(movie,index) in MovieList' :key="index">
            <div id="left"><img :src="movie.pic_url"></div>
            <div id="right">
                <ul>
                    <li>{{movie.movieName}}<img src="img/20181101103219.jpg" alt=""></li>
                    <li>公众评<span>{{movie.xianqing.rating}}</span></li>
                    <li>{{movie.xianqing.actors}}...</li>
                    <li>{{movie.xianqing.plot_simple}}</li>
                </ul>
                <div class="gp">详情</div>
            </div>
        </div>
        <div class="loading" v-show="loadingShow"><img src="@/assets/img/loading.gif" ></div>
    </main>
</template>

<script>
import Axios from 'axios'
export default {
    data(){
        return{ 
            MovieList:[],
            loadingShow:false,
        }
       
    },
    methods:{
        loadData(){
              //let url=global.APT_PROXY+'http://m.maoyan.com/mmdb/comments/movie/248566.json?_v_=yes&offset=1';
         let url2='./static/data/taday.json'
         Axios.get(url2).then(
             res=>{
                         
                this.MovieList=res.data.result;
                this.loadingShow=true;
                console.log(this.MovieList)
                     }
         ).catch()
        },
    goDetail(movieId){
        this.$router.push('/movie/MovieDetail/'+movieId)
    },
    },
    mounted(){
      this.loadData();
      window.onscroll=()=>{
          let clientHeight=document.documentElement.clientHeight;
          let scrollHight=document.documentElement.scrollHeight;
          let scrollTop=document.documentElement.scrollTop;
          if(clientHeight+scrollTop==scrollHight){
              this.loadingShow=true;
              console.log('到底了');
          }

      }
    },  
}
</script>

<style scoped>
main {    overflow: hidden;margin-top: 1.3rem;
        }
        main #list {
            width: 95%;
            height: 114px;
            margin: auto;
        }

        main #left {
            float: left;
            margin-left:0.3rem;
            width: 23%;
            height:100px;
            overflow: hidden;
        }

        main #left img {
            width: 100%;
        }

        main #right {
            float: right;
            width:50%;
            margin-right: 17%;
   
            border-bottom: 1px solid #F0F0F0;
            position: relative;
     
        }
     
     
        main #right li:nth-of-type(1) {
            font-size:0.3rem;
            font-weight: bold;
            color: #000000;
            margin-top: 2%;
            width:100%;
            height:0.5rem;
            overflow: hidden;
        }

        main #right li:nth-of-type(1) img {
            vertical-align: middle;
        }

        main #right li:nth-of-type(2) {
            color: #666666;
            font-size:0.2rem;
            margin-top: 1%;
        }

        main #right li:nth-of-type(2) span {
            color: #FAAF00;
            font-weight: bold;
        }

        main #right li:nth-of-type(3) {
     
            width:70%;
            height: 20px;
            overflow:hidden;
            color: #666666;
            font-size: 0.2rem;
            margin-top: 1%;
        }

        main #right li:nth-of-type(4) {
            color: #666666;
            font-size: 0.2rem;
            margin-top: 1%;
            width:70%;
            height: 20px;
            overflow:hidden;
        }

        main #right .gp {
            position: absolute;
            width: 28%;
            height: 28px;
            background: #F03D37;
            font-size:0.2rem;
            color: #ffffff;
            text-align: center;
            line-height: 28px;
            border-radius: 20%;
            top: 35%;
            left: 100%;
        }
        main .loading{
            text-align: center;
            margin-bottom: 1rem;
        }
</style>



