<template>
<div>
  <div id="banner">
        <div id="left" > <img :src="MovieDatay.pic_url"></div>
        <div id="right">
            <ul>
            <li>{{MovieDatay.movieName}}</li>
            <li>{{MovieDatay.xianqing.directors}}</li>
            <li>{{MovieDatay.xianqing.rating}}  <span>(评价)</span></li>
            <li>{{MovieDatay.xianqing.genres}}</li>
            <li>{{MovieDatay.xianqing.country}}/{{MovieDatay.xianqing.runtime}}</li>
            <li>{{MovieDatay.xianqing.release_date}}大陆上映</li>
            </ul>
            <div>
                <img src="static/img/bg2.png" alt="">
            </div>
        </div>
    </div>
      <nav>
        <ul>
            <li>今天11月1日</li>
            <li>明天11月2日</li>
            <li>后天11月3日</li>
            <li>周日11月4日</li>
            <li>周一11月5日</li>
        </ul>
    </nav>
    <div id="nav2">
        <ul>
            <li :v-model="City" @click="show">{{City.city_name}}<img src="/static/img/20181101151322.jpg" alt=""><span></span>
                <ul class="list"  v-if="bb">
                  <li>请选择城市</li>
                  <li @click="City=city" v-for="(city,index) in MovieCity" :key="index">{{city.city_name}}</li>
                </ul>
            </li>
            <li>品牌<img src="/static/img/20181101151322.jpg" alt=""><span></span></li>
            <li>特色<img src="/static/img/20181101151322.jpg" alt=""></li>
        </ul>
    </div>
  
        <div id="list">
            <ul :v-model="newayy=City" v-for="ayy in MovieCity" :key=ayy.id >
                <li>{{newayy.cinema}} <span>31元起</span></li>
                <li>南岗区中兴左街2号金爵万象商业广场8层  1.4km</li>
                <li><img src="/static/img/20181101152909.jpg" alt=""></li>
                <li><img src="/static/img/bg(4).png" alt="">开卡特惠，首单2张立减2元</li>
                <li>近期场次：15：30 | 16:30 | 17:00</li>
            </ul>
        </div>
</div>
</template>

<script>
import CommonMovie from '../common/Movie'
import Axios from 'axios'
export default {
    data(){
        return{
            MovieDatay:{
            xianqing:{}
            },
            MovieCity:[],
            City:{
                city_name:'哈尔滨',
            },
            bb:false,
            newayy:{},
        }
    },
    mounted(){ 
        let url="./static/data/taday.json"
        let url2="./static/data/city.json"
        Axios.get(url).then(res=>{
        this.MovieDatay=res.data.result[this.$route.params.movieId];
        console.log(this.MovieDatay);
        }).catch();
        Axios.get(url2).then(res1=>{
        this.MovieCity=res1.data.result;
        console.log(this.MovieCity);
        }).catch()
            },
        components:{
        CommonMovie,
       },
       methods:{
        show:function(){
            this.bb=!this.bb
        }
       }

}
</script>

<style scoped>
 #banner{width:100%;height:4rem;background:#4A4C52;margin-top: 1rem;}
#banner #left{float:left;width: 35%;margin: 3%;height:3.5rem;}
#banner #left img{width: 100%;height: 100%;}
#banner #right{float:right;width:50%;margin: 3%;margin-right: 6%;position: relative;}
#banner #right li{margin-top:4%;}
#banner #right li:nth-of-type(1){color: #ffff;font-size:0.45rem;font-weight: bold;height:1rem;}
#banner #right li:nth-of-type(2){color: #D9DADB;font-size: 0.2rem;}
#banner #right li:nth-of-type(3){color: #FFCC00;font-size: 0.2rem;font-weight: bold;}
#banner #right li:nth-of-type(3) span{color: #D9DADB;font-size: 0.2rem;}
#banner #right li:nth-of-type(4){color: #D9DADB;font-size:0.2rem;}
#banner #right li:nth-of-type(5){color: #D9DADB;font-size:0.2rem;}
#banner #right li:nth-of-type(6){color: #D9DADB;font-size:0.2rem;}
#banner #right div{float: right;position: absolute;top:45%;right: 1%;}
 nav{width: 100%;height: 51px;border-bottom: 1px solid #F0F0F0;overflow-x: scroll;}   
nav ul{width:1000px; }
nav li{width:134px;line-height: 50px;color: #666666;float: left;text-align: center;}
nav li:hover{border-bottom: 1px solid #E54847; color:#E54847;}
#nav2{width: 100%;height: 50px;border-bottom: 1px solid #F0F0F0;}
#nav2 li{float: left;width: 33%;text-align: center;line-height: 50px;color: #666666;}
#nav2 li:nth-of-type(1) select{outline: none;border: none;width:1.8rem;height:0.8rem;text-align: center;
}
#nav2 li:nth-of-type(1) ul{width: 100vw;height:5rem;background:#FFFFFF;position: relative;}
#nav2 li:nth-of-type(1) ul li:nth-of-type(1){width: 100vw;height: 1rem;color:#666666;border-bottom: 0.005rem solid #F3F3F3;
}
#nav2 li:nth-of-type(1) select option{border:0.01rem solid #666666;}
#nav2 li img{vertical-align: middle;}
#nav2 li span{float: right;height: 30px;background: url(/static/img/bg.png);width: 2px;margin-top:10px;}
#list {width: 100%;height:3rem;border-bottom:1px solid #F0F0F0; }
#list ul{margin-left: 2%;}
#list li:nth-of-type(1){color: #000000;font-size:0.2rem;margin-top:6%;}
#list li:nth-of-type(1) span{color: #E54847;}
#list li:nth-of-type(2){color: #666666;font-size:0.2rem;margin-top:2%;}
#list li:nth-of-type(4){color: #999999;font-size:0.2rem;margin-top:2%;}
#list li:nth-of-type(4) img{width:5%;vertical-align: middle;}
#list li:nth-of-type(5){color: #999999;font-size:0.2rem;margin-top:2%;}

</style>




