<template>
    <main>
        <ul v-for="hot in HotList" :key="hot.id">
            <li><video :src="hot.play" controls width="100%" height="">
                </video></li>
                    <li>
                    <p><img :src="hot.img"></p>
                    <p>{{hot.title}}</p>
                    <p><img src="/static/img/a.jpg" alt="">    {{hot.num}}</p>
                    <p><img src="/static/img/b.jpg" alt=""></p>
             </li>
        </ul>q

    </main>
</template>

<script>
import Axios from 'axios' ;
export default {
   data(){
        return{
            HotList:[],
        }
    },
    mounted(){
        let url="./static/data/hotdata.json"
        Axios.get(url).then(res=>{
            this.HotList=res.data.data
        }).catch()
        console.log(this.HotList);
    },
}
</script>

<style scoped>
main{margin-top: 1rem;margin-bottom:1rem; }
main ul{margin-top:0.5rem; }
main ul li:nth-of-type(1){width:100%;height: 3rem;margin-top:0.1rem; }
main ul li:nth-of-type(2){width: 100%;height: 1rem;margin-top:0.65rem;line-height: 1rem;position: relative;}
main ul li:nth-of-type(2) p{float: left;}
main ul li:nth-of-type(2) p:nth-of-type(1){width: 0.7rem;height: 0.7rem;border-radius:50%;overflow: hidden;
float: left;position: absolute;left: 0.3rem;top:0.15rem;}
main ul li:nth-of-type(2) p:nth-of-type(1) img{width: 100%;height: 100%;}
main ul li:nth-of-type(2) p:nth-of-type(2){position: absolute;left: 1.3rem;}
main ul li:nth-of-type(2) p:nth-of-type(3){float: right;position: absolute;right: 1.5rem;}
main ul li:nth-of-type(2) p:nth-of-type(3) img{vertical-align: middle;}
main ul li:nth-of-type(2) p:nth-of-type(4) img{position: absolute;right: 0.5rem;top:0.28rem}
</style>



