import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Movie from '@/components/movie/Movie'
import MovieList from '@/components/movie/MovieList'
import MovieDetail from '@/components/movie/MovieDetail'
import MovieDetail1 from '@/components/movie/MovieDetail1'


import Index from '@/components/index/Index'
import IndexList from '@/components/index/IndexList'
import IndexDetail from '@/components/index/IndexDetail'

import My from '@/components/my/My'
import MyList from '@/components/my/MyList'
import Hot from "@/components/hot/Hot"
import HotList from "@/components/hot/HotList"




Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect:'/index/indexlist'
    },{
      path:'/movie',
      name:'Movie',
      component:Movie,
      children:[{
        path:'movielist',
        name:'MovieList',
        component:MovieList,
      },{
        path:'moviedetail/:movieId',
        name:'MovieDetail',
        component:MovieDetail,
        
          }]
    },{
   
        path:'/moviedetail1',
        name:'MovieDetial1',
        component:MovieDetail1,

    },
    {
      path:'/index',
      name:'Index',
      component:Index,
      children:[{
        path:'indexlist',
        name:'IndexList',
        component:IndexList,
      },{ 
      path:'indexdetail',
      name:'IndexDetail',
      component:IndexDetail}]
    },{
      path:"/my",
      name:"My",
      component:My,
      children:[{
        path:'mylist',
        name:"MyList",
        component:MyList
      }]
    },{
      path:'/hot',
      name:"Hot",
      component:Hot,
      children:[{
        path:'hotlist',
        name:"HotList",
        component:HotList
      }]
    }
  ]
})



